.section{
    width: 100%;
    background-color: #F8F9F9;
}
.firstDiv{
    width: 100%;
    display: flex;
    flex-direction: row;
    margin-top: 70px;
    height: 150px;

}
.subDiv1{
    width: 15%;
    display: flex;
    height: 100px;
    align-items: center;
}
.textDiv1{
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding-left: 15px;
   justify-content: center;
   color: gray;
}
.subDiv2{
    width: 40%;
    height: 100%;
    border-radius: 100%;
}
.affdashcardImg1{
    width: 100%;
    height: 100%;
    border-radius: 100%;
}
.earningDiv{
    width: 85%;
    height: 100%;
    display: flex;
    align-items: center;
    background-color: #F4F6F7;
    padding-top: 40px;
    padding-bottom: 40px;
    justify-content: space-around;
   
   

}
.available{
    display: flex;
    flex-direction: column;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    height: 100%;
    width: 30%;
    padding: 15px;
    border-radius: 25px;
}
.earningText2{
    color: #797D7F;
    font-size: 20px;
    font-family: Arial, Helvetica, sans-serif;
}
.availableSub1{
    height: 90%;
    width: 100%;
    display: flex;
    align-items: center;
    color: #1C2833;
    font-size: 18px;
    font-family: Arial, Helvetica, sans-serif;
    justify-content: space-between;
}

.earningtext3{
    font-size: 25px;
}
.icon{
    font-size: 70px;
    margin-right: 15px;
}
.offerList{
    background-color: #F8F9F9;
    width: 100%;
    margin-top: 100px;
    height: 550px;
}
.oferText{
    margin-left: 20px;
}